.popup__container{
    margin: 0;
    padding: 0;
    max-width: 430px;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    position: relative;
}
@media screen and (max-width: 550px) {
    .popup__container{
        max-width: 282px;
    }
}